.trigger {
    padding: 0 24px;
    font-size: 18px;
    line-height: 64px;
    cursor: pointer;
    transition: color 0.3s;
  }
  
  .trigger:hover {
    color: #288af9;
  }
  
  .logo {
    height: 32px;
    margin: 16px;
    color: white;
    font-size: large;
  }
  
  .site-layout .site-layout-background {
    background: #fff;
  }
  
  .side-pannel, .side-menu{
    background-color: var(--secondary) !important;
  }
